import React, { Component } from "react";
import "isomorphic-unfetch";
import * as Styled from "./styled/Results.js";
import { getAPIHostname } from "modules/getAPI";
import { observable, action, computed, useStrict } from "mobx";

// const query = {
// 	start: 0,
// 	limit: 50
// };
// const queryAdvance = function() {
// 	query.start += query.limit;
// };

class Query extends React.Component {
	handleUsernameSubmission(e) {
		if (e) {
			e.preventDefault();
		}
		const name = this.refs.skill.value;
	}
	render() {
		return (
			<div>
				<form onSubmit={this.handleUsernameSubmission}>
					<input placeholder="skill..." ref="skill" />
					<input type="submit" value="Search" />
				</form>
			</div>
		);
	}
}

class Results extends Component {
	// static async getInitialProps({ match }) {
	// 	const res = await fetch(getAPIHostname() + `/api/v1/jobs?text=&start=${query.start}&limit=${query.limit}`);
	// 	const json = await res.json();
	// 	return { jobs_count: json.results, jobs: json.data || [] };
	// }
	constructor() {
		super();
		this.state = {
			jobs: []
		};
	}

	async componentWillMount() {
		const res = await fetch(getAPIHostname() + `/api/v1/jobs?text=&start=0&limit=10000`);
		const json = await res.json();
		this.setState({ jobs: json.data || [] });
	}

	render() {
		var jobs = this.state.jobs || this.props.jobs;
		// get on with it...
		var Jobs = [];
		if (jobs) {
			jobs.forEach(function(job, i) {
				Jobs.push(
					<div key={job._id}>
						<div>{job.text}</div>
						<sup>{job.location}</sup>
					</div>
				);
			});
		}
		return (
			<Styled.Results>
				<h2>Query:</h2>
				<Query />
				<h2> </h2>

				<h2>Results: {jobs.length}</h2>
				<div>{Jobs}</div>
			</Styled.Results>
		);
	}
}

export default Results;



